<template>
  <div class="editorResult">
    <Overview
      v-if="overviewData !== null"
      :chart-data="overviewData"
      :quiz-total-score="quizTotalScore"
    />
    <OverviewList
      v-if="answerGroups.length > 0"
      :answer-groups="answerGroups"
      :quiz-group-id="quizGroupId"
    />
    <ExportData
      class="mb-6"
      :quiz-group-id="quizGroupId"
      :quiz-event-id="quizEventId"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import Overview from '@/components/Overview.vue';
import OverviewList from '@/components/OverviewList.vue';
import AnswerGroupApi from '@/libs/AnswerGroupApi';
import Utils from '@/libs/Utility';
import ExportData from '@/components/ExportData.vue';
import QuizGroupApi from '@/libs/QuizGroupApi';

export default {
  components: {
    Overview,
    OverviewList,
    ExportData,
  },
  data() {
    return {
      overviewData: null,
      quizGroupId: null,
      quizEventId: null,
      answerGroups: [],
      quizTotalScore: 0,
    };
  },
  computed: {
    currentLang() {
      return this.$store.state.currentLang;
    },
  },
  watch: {
    async currentLang() {
      await this.reDrawChart();
    },
  },
  created() {
  },
  async mounted() {
    await this.reDrawChart();
  },
  methods: {
    async reDrawChart() {
      this.$store.commit('updateLoading', true);
      try {
        await this.fetchData();
      } catch (error) {
        this.$router.push({ name: 'NoData' });
        console.error(error);
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    async fetchData() {
      const { quizGroupId, quizEventId } = this.$route.query;
      this.quizGroupId = quizGroupId;
      this.quizEventId = quizEventId;

      if (process.env.NODE_ENV === 'development') {
        if (!this.quizGroupId && process.env.VUE_APP_QUIZ_GROUP_ID) {
          this.quizGroupId = process.env.VUE_APP_QUIZ_GROUP_ID;
        }
      }

      if (this.quizGroupId) {
        this.answerGroups = await AnswerGroupApi.listAnswerGroupsByQuizGroup(this.quizGroupId);
        this.quizGroup = await QuizGroupApi.retrieve(this.quizGroupId);
        this.quizTotalScore = this.quizGroup.totalScore;
      } else if (this.quizEventId) {
        this.answerGroups = await AnswerGroupApi.listAnswerGroupsByQuizEvent(this.quizEventId);
        // TODOeventquizListLength
        // this.answerGroup.sourceQuizGroupIdthis.quizGroupIdthis.quizEventId

        // this.quizGroup = await QuizGroupApi.retrieve(this.answerGroup.sourceQuizGroupId);
        // this.quizTotalScore = this.quizGroup.quizList.length;
      } else {
        // failed to fetch data
        throw new Error('');
      }

      const TIME_LIMIT = 6600; // 110 min

      this.answerGroups.forEach((answerGroup) => {
        answerGroup.timeSpent = Utils.formatFloat(answerGroup.timeSpent);
      });

      this.overviewData = this.answerGroups.map((answerGroup) => ({
        x: answerGroup.timeSpent > TIME_LIMIT ? TIME_LIMIT : answerGroup.timeSpent, // do not display if spent more than 6600 seconds
        y: answerGroup.scoreGot,
      }));
    },
  },
};
</script>

