<template>
  <tr v-if="item">
    <td>{{ index+1 }}</td>
    <td>{{ item.user.name }}</td>
    <td>{{ item.user.businessDefinedId }}</td>
    <td>{{ float(item.scoreGot) }}</td>
    <td>{{ float(item.correctRate * 100) }}%</td>
    <td>{{ item.timeSpent }}</td>
    <td>
      <button class="btn btn-primary"
              @click.prevent="viewDetail()"
      >
        {{ $t('') }}
      </button>
    </td>
  </tr>
</template>

<script>
import Utils from '@/libs/Utility';

export default {
  components: {
  },
  props: {
    item: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
    };
  },
  computed: {
  },
  methods: {
    viewDetail() {
      this.$router.push({
        name: 'UserResult',
        params: {
          answerGroupId: this.item.uuid,
        },
      });
    },
    float(data) {
      return Utils.formatFloat(data);
    },
  },
};
</script>

<style scoped lang="scss">
td{
  padding: 10px;
}
table, td, th{
  border-bottom: 1px solid #777;
  padding-bottom: 10px;
  border-collapse: collapse;
}
.btn-primary{
  border: 1px solid #1976d2;
  color: #1976d2;
  padding: 0px 3px;
  border-radius: 3px;
}
</style>
