<template>
  <div class="export-data">
    <div class="container">
      <h1>{{ $t('') }}</h1>
      <v-btn @click.prevent="exportAllData">
        {{ $t('') }}
      </v-btn>
    </div>
  </div>
</template>

<script>
import AnswerGroupApi from '@/libs/AnswerGroupApi';
import { parse } from 'json2csv';
import Utils from '@/libs/Utility';

export default {
  components: {
  },
  props: {
    quizGroupId: {
      type: String,
      default: null,
    },
    quizEventId: {
      type: String,
      default: null,
    },
  },
  computed: {
  },
  methods: {
    async exportAllData() {
      this.$store.commit('updateLoading', true);
      try {
        const detailAnswerGroups = await this.fetchData();
        await Promise.all(
          [this.exportData(detailAnswerGroups, 'userAnswerSet', this.$t('')),
            this.exportData(detailAnswerGroups, 'timeSpent', this.$t('')),
            this.exportData(detailAnswerGroups, 'startTime', this.$t('')),
            this.exportData(detailAnswerGroups, 'endTime', this.$t(''))],
        );
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    async fetchData() {
      let answerGroups = null;

      if (this.quizGroupId) {
        answerGroups = await AnswerGroupApi.listAnswerGroupsByQuizGroup(this.quizGroupId);
      } else if (this.quizEventId) {
        answerGroups = await AnswerGroupApi.listAnswerGroupsByQuizEvent(this.quizEventId);
      } else {
        throw new Error('quizGroupId and quizEventId are both not given');
      }

      const requests = [];

      answerGroups.forEach((answerGroup) => {
        requests.push(AnswerGroupApi.retrieve(answerGroup.uuid));
      });

      const detailAnswerGroups = await Promise.all(requests);
      console.log(detailAnswerGroups);
      return detailAnswerGroups;
    },
    async exportData(data, fieldName, outputName) {
      const csvData = [];
      let maxLength = 0;
      data.forEach((answerGroup) => {
        const csvFormatData = {
          : answerGroup.user.name,
          : answerGroup.user.businessDefinedId,
        };

        for (let i = 0; i < answerGroup.answerList.length; i += 1) {
          csvFormatData[i + 1] = answerGroup.answerList[i][fieldName];
        }

        csvData.push(csvFormatData);
        maxLength = Math.max(maxLength, answerGroup.answerList.length);
      });

      const fields = [this.$t(''), this.$t('')];
      for (let i = 0; i < maxLength; i += 1) {
        fields.push(String(i + 1));
      }
      const opts = { fields };

      try {
        const csv = parse(csvData, opts);
        console.log(csv);
        this.$notify({
          type: 'success',
          title: this.$t(''),
        });
        Utils.exportCsv(`${outputName}.csv`, csv);
      } catch (err) {
        console.error(err);
      }
    },
  },
};
</script>

<style scoped lang="scss">
.export-data {
  .container{
    max-width: 1200px;
    padding: 0 20px;
    margin: auto;
    h1{
      display: flex;
      justify-content: flex-start;
      border-bottom: 2px solid black;
      padding-bottom: 10px;
      margin-top: 20px;
      margin-bottom: 30px;
      font-size: 1.8rem;
    }

    .group {
      margin-top: 30px;
      h2 {
        margin-bottom: 10px;
        font-size: 1.2rem;
        text-align: center;
      }
      .chart {
        max-width: 600px;
        margin: auto;
        img {
          width: 100%;
        }
        .txt{
          margin-top: 20px;
          p{
            text-align: left;
          }
        }
      }
    }
  }
}
</style>
