<template>
  <div class="overviewList">
    <div class="container">
      <h1>{{ $t('') }}</h1>
      <div class="content">
        <div class="table-area">
          <table class="table">
            <tr>
              <th>{{ $t('') }}</th>
              <th>{{ $t('') }}</th>
              <th>{{ $t('') }}</th>
              <th>{{ $t('') }} ({{ $t('') }} {{ quizTotalScore }})</th>
              <th>{{ $t('') }}</th>
              <th>{{ $t('') }}</th>
              <th>{{ $t('') }}</th>
            </tr>
            <ListItem
              v-for="(item, index) in answerGroups" :key="index"
              :item="item" :index="index"
            />
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ListItem from '@/components/ListItem.vue';
import QuizGroupApi from '@/libs/QuizGroupApi';

export default {
  components: {
    ListItem,
  },
  props: {
    answerGroups: {
      type: Array,
      required: true,
    },
    quizGroupId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      quizTotalScore: 0,
    };
  },
  async mounted() {
    await this.getData();
  },
  methods: {
    async getData() {
      const quizGroup = await QuizGroupApi.retrieve(this.quizGroupId);
      this.quizTotalScore = quizGroup.totalScore;
    },
  },

};
</script>

<style scoped lang="scss">
.overviewList {
  .container{
    max-width: 1200px;
    padding: 0 20px;
    margin: auto;
    h1{
      display: flex;
      justify-content: flex-start;
      border-bottom: 2px solid black;
      padding-bottom: 10px;
      margin-top: 20px;
      margin-bottom: 30px;
      font-size: 1.8rem;
    }
    .content {
      padding: 0 20px;
      margin: auto;
      .table-area{
        margin-bottom: 20px;
        table{
          width: 100%;
          // max-width: 500px;
          margin: auto;
          text-align: center;
        }
        td{
          padding: 10px;
        }
        table, td, th{
          border-bottom: 1px solid #777;
          padding-bottom: 10px;
          border-collapse: collapse;
        }
      }
    }

  }
}
</style>
